@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
}

.hero-image-container {
  overflow-x: hidden;
}

svg {
  margin-top: -7%;
  margin-right: -2%;
}

header {
  position: absolute;
  width: 100%;
}

#hamburger-menu {
  display: none;
}

ul {
  font-size: 28px;
  font-weight: 600;
  list-style: none;
  display: flex;
  justify-content: right;
  flex-direction: row;
  grid-gap: 6%;
  grid-gap: 6%;
  gap: 6%;
  margin-top: 30px;
  margin-right: 4.5vw;
}

a {
  text-decoration: none;
}

li {
  color: black;
  text-decoration: none;
  transition: all ease-out 0.4s;
  border-bottom: 2px solid transparent;
}

li:hover {
  border-color: #497ae3;
}

h1 {
  text-align: center;
}

.section {
  padding-top: 10vh;
}

#projects {
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 10px;
}

hr {
  width: 80%;
  margin: 0 auto;
  margin-bottom: 15vh;
}

#about-hr {
  border: none;
  height: 1px;
  background-color: white;
  margin-bottom: 7vh;
}

.projects-container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 40px calc((100% - (255px * 3)) / 4);
  grid-column-gap: 120px;
  -webkit-column-gap: 120px;
          column-gap: 120px;
  grid-row-gap: 120px;
  row-gap: 120px;
  margin-bottom: 100px;
}

.project-container {
  width: 250px;
  height: 250px;
  background-color: #bfbfbf;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px,
    rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  border-radius: 10px;
}

.project-title {
  padding: 20px;
  text-shadow: none;
}

.button {
  color: white;
  background-color: #497ae3;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  font-size: larger;
  width: 60%;
  min-height: 7vh;
}

.learn-more-button {
  margin: 0 auto;
  margin-bottom: 20px;
}

.about-section {
  background-color: #bfbfbf;
}

#about {
  color: white;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 10px;
}

.about-container {
  margin: 0 auto;
  width: 500px;
  max-width: 80vw;
}

#download-cv-form {
  width: 47%;
  padding: 0;
}

.download-cv-button {
  width: 100%;
}

p {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
  background-color: white;
  padding: 40px;
  margin-bottom: 20px;
}

#about-buttons-container {
  display: flex;
  justify-content: space-between;
  grid-row-gap: 10px;
  row-gap: 10px;
  padding: 20px;
  /*for smaller screen sizes padding: 20px 0;*/
}

.about-buttons {
  width: 47%;
}

/* project modal styles */

body.active-modal {
  overflow: hidden;
}

.modal,
.overlay {
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  position: fixed;
}

.modal {
  z-index: 4;
}

.overlay {
  background: rgba(49, 49, 49, 0.8);
}

.modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #ffffff;
  width: 90vw;
  max-width: 95vw;
  min-height: -webkit-fit-content;
  min-height: -moz-fit-content;
  min-height: fit-content;
  display: flex;
  flex-direction: row;
}

.gif-container {
  width: 70%;
}

.right-side-container {
  width: 30%;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

video {
  height: 100%;
  width: 100%;
  object-fit: fill;
}

.modal-button {
  width: 100%;
  margin: 8px 0;
}

.project-buttons {
  padding: 0;
}

.a-buttons {
  color: white;
  text-decoration: none;
}

.description-title {
  text-align: left;
  margin-bottom: 10px;
  color: #497ae3;
}

.github-button {
  background-color: black;
}

.close-modal-icon {
  position: absolute;
  top: 0;
  right: 0;
  margin-top: 10px;
  margin-right: 15px;
}

.close-modal-icon:hover {
  cursor: pointer;
}

/* contact modal */

.modal-content-contact {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: #ffffff;
  height: 95vh;
  width: 50vw;
}

.form-container {
  height: 95vh;
}

form {
  display: flex;
  flex-direction: column;
  padding: 0 20%;
  max-height: 80%;
}

textarea {
  height: 40vh;
  min-height: 5vh;
  resize: vertical;
  background-color: #bfbfbf;
  border-radius: 10px;
  border: none;
  padding: 5px;
  margin-bottom: 10%;
  color: white;
  font-size: large;
}

input {
  min-height: 5vh;
  border: none;
  border-radius: 10px;
  background-color: #bfbfbf;
  margin-bottom: 20px;
  padding: 5px;
  color: white;
  font-size: larger;
}

input:focus,
textarea:focus {
  border: none;
  border-radius: 2pt;
  box-shadow: 0 0 2pt 1pt black;
  outline: none;
}

label {
  margin-bottom: 5px;
}

.send-button {
  width: 100%;
}

.h1-contact {
  margin: 20px 0;
  font-size: 3vw;
}

/* Change Autocomplete styles in Chrome*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: rgb(255, 255, 255);
  -webkit-transition: background-color 5000s ease-in-out 1s;
  transition: background-color 5000s ease-in-out 1s;
}

/*dont show video for screens under 1085px*/
@media only screen and (max-width: 1085px) {
  body {
    font-size: 0.8em;
  }

  @supports (overflow-x: clip) {
    .hero-image-container {
      overflow-x: clip;
    }
  }
  @supports not (overflow-x: clip) {
    .hero-image-container {
      clip: auto;
      overflow-x: visible;
      overflow-x: initial;
    }
  }

  /* .hero-image-container {
    overflow-x: clip;
    clip: auto;
  } */

  .gif-container {
    display: none;
  }

  .modal-content {
    min-height: 80vh;
  }
}

/*project modal in range 550-1085px*/
@media only screen and (max-width: 1085px) and (min-width: 550px) {
  .modal-content {
    height: 80vh;
    max-width: 500px;
  }

  .right-side-container {
    width: 100%;
    grid-gap: 40px;
    gap: 40px;
  }
}

/*project modal under 550px*/
@media only screen and (max-width: 550px) {
  .right-side-container {
    width: 100%;
    grid-gap: 20px;
    gap: 20px;
  }
}

/*contact form width under 800px*/
@media only screen and (max-width: 800px) {
  .modal-content-contact {
    width: 100vw;
    height: 100vh;
  }

  .h1-contact {
    font-size: 25px;
  }
}

/*hero-image scale in range 900-600px*/
@media only screen and (max-width: 900px) and (min-width: 600px) {
  .hero-image-container {
    margin-bottom: 20vh;
  }

  svg {
    transform: scale(1.5);
    -webkit-transform: scale(1.5); /* Safari & Chrome */
    -moz-transform: scale(1.5); /* Firefox */
    -ms-transform: scale(1.5); /* Internet Explorer */
    -o-transform: scale(1.5); /* Opera */
  }
}

/*hero-image scale in range 600-400px*/
@media only screen and (max-width: 600px) and (min-width: 400px) {
  .hero-image-container {
    margin-bottom: 25vh;
  }

  svg {
    transform: scale(2);
    -webkit-transform: scale(2); /* Safari & Chrome */
    -moz-transform: scale(2); /* Firefox */
    -ms-transform: scale(2); /* Internet Explorer */
    -o-transform: scale(2); /* Opera */
    margin-top: 0;
  }
}

/*hero-image scale in range 400-320px*/
@media only screen and (max-width: 400px) {
  .hero-image-container {
    margin-bottom: 25vh;
  }

  svg {
    transform: scale(2.5);
    -webkit-transform: scale(2.5); /* Safari & Chrome */
    -moz-transform: scale(2.5); /* Firefox */
    -ms-transform: scale(2.5); /* Internet Explorer */
    -o-transform: scale(2.5); /* Opera */
    margin-top: 30px;
  }
}

/*responsive hamburger menu*/
@media only screen and (max-width: 1085px) {
  #hamburger-menu {
    display: inline;
    display: initial;
    position: fixed;
    top: 0;
    right: 0;
    margin-right: 20px;
    z-index: 2;
    font-size: 7vh;
    color: black;
  }

  #header {
    z-index: 1;
    background-color: #497ae3;
    position: fixed;
    display: none;
  }

  ul {
    flex-direction: column;
    margin: 0;
    margin-top: calc(8vh + 10px);
  }

  li {
    border-bottom: none;
    transition: none;
    color: white;
    padding: 10px 0;
    text-align: center;
  }

  @-moz-document url-prefix() {
    /*fixes the bug that the project nav link is bigger*/

    ul {
      display: block;
    }
  }

  li:hover {
    border-color: white;
  }

  .hamburger-menu-border {
    border-top: 1px solid white;
    border-bottom: 1px solid white;
  }

  .hamburger-menu-border-bottom {
    border-bottom: none;
  }
}

